import { Link, NavLink } from "react-router-dom";
import styles from "./profile-menu.module.css";
import { logout } from "../../services/actions/auth";
import { useDispatch } from "react-redux";

function ProfileMenu({ description }: { description: string }) {
  const dispatch = useDispatch();
  return (
    <div className={styles.leftColumn}>
      <div className={styles.sideMenu}>
        <NavLink to="/profile" className={styles.active__link} end>
          {({ isActive }) =>
            isActive ? (
              <h1 className="text text_type_main-medium"></h1>
            ) : (
              <span className="text text_type_main-medium text_color_inactive">
                
              </span>
            )
          }
        </NavLink>
        <NavLink to="/profile/orders" className={styles.active__link}>
          {({ isActive }) =>
            isActive ? (
              <h1 className="text text_type_main-medium"> </h1>
            ) : (
              <span className="text text_type_main-medium text_color_inactive">
                 
              </span>
            )
          }
        </NavLink>
        <Link
          to="/login"
          className={`text text_type_main-medium text_color_inactive ${styles.link}`}
          onClick={(e) => {
            e.preventDefault();
            dispatch(logout());
          }}
        >
          
        </Link>
      </div>
      <p className="text text_type_main-default text_color_inactive">
        {description}
      </p>
    </div>
  );
}

export { ProfileMenu };
